.sidebar {
  background-color: #ffffff;
  width: 24%;
  margin-top: 122px;
  padding: 24px 16px;
  border-radius: 18px;
  height: 100%;
  .top_sidebar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--btn-bg-back);
    width: 100%;
    padding-bottom: 15px;
    p {
      font-family: Nunito;
      font-size: 24px;
      font-weight: 600;
      line-height: 24px;
      letter-spacing: 0em;
      text-align: left;
    }
    span {
      padding: 5px 20px;
      background-color: var(--btn-bg-back);
      text-align: center;
      border-radius: 6px;
      font-family: Nunito;
      font-size: 12px;
      font-weight: 400;
      line-height: 16px;
    }
  }

  .sidebar_content {
    height: 340px;
    overflow-y: scroll;
    padding-right: 20px;
    width: 260px;
    overflow-x: hidden;
    &::-webkit-scrollbar {
      width: 4px;
      background-color: var(--btn-bg-back);
      border-radius: 4px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--btn-bg);
      border-radius: 4px;
    }
    li {
      display: flex;
      align-items: center;
      width: 100%;
      justify-content: space-between;
      height: 84px;
      border-bottom: 1px solid var(--btn-bg-back);
      img {
        max-width: 70px;
        border-radius: 8px;
      }

      .btn_delete {
        background-color: var(--btn-bg-back);
        cursor: pointer;
        border-radius: 8px;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        svg {
          transition: 0.3s;
          height: 22px;
          &:hover {
            transform: scale(1.1);
            cursor: pointer;
          }
        }
      }

      .sidebar__item_content {
        display: flex;
        .item__date {
          margin-left: 10px;
          p {
            font-family: Nunito;
            font-size: 12px;
            font-weight: 400;
            line-height: 16px;
            text-align: left;
          }
          span {
            color: #b1b1b1;
          }
        }
      }
    }
    .ops {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      height: 100%;
      img {
        width: 30%;
      }
    }
  }

  .sidebar__item_footer {
    display: flex;
    flex-direction: column;
    .footer_top {
      display: flex;
      justify-content: space-between;
      margin-top: 16px;
      margin-bottom: 24px;
      p {
        font-family: Nunito;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        letter-spacing: 0em;
        text-align: left;
      }
    }
    button {
      height: 40px;
      border: none;
      outline: none;
      border-radius: 12px;
      background-color: var(--btn-bg);
      font-family: Nunito;
      font-size: 16px;
      font-weight: 400;
      color: #fff;
      margin-bottom: 20px;
      cursor: pointer;
    }

    span {
      display: flex;
      align-items: center;
      img {
        margin-right: 10px;
      }
    }
  }
}

@media (max-width: 986px) {
  .sidebar {
    width: auto;
    margin-top: 40px;
    .sidebar_content {
      width: auto;
    }
  }
}
