@import '../Footer/footer.module.scss';

.header {
  background-image: url('../../../public/images/elips.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  margin-bottom: 40px;

  .logo {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 488px;
    img {
      z-index: 1;
      display: block;
      position: relative;
    }

    .header_post {
      display: flex;
      align-items: center;
      margin-top: 57px;
      width: 784px;
      justify-content: space-around;
      .text_block {
        font-family: Nunito;
        font-size: 50px;
        font-weight: 800;
        line-height: 60px;
        letter-spacing: 0em;
        text-align: left;
        color: #fff;
        span {
          color: #ff5c00;
        }
        p {
          font-family: Nunito;
          font-size: 16px;
          font-weight: 400;
          letter-spacing: 0em;
          text-align: left;
          margin-top: 52px;
        }
        @media (max-width: 731px) {
          font-size: 28px;
        }
      }
    }
  }

  .elips {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
  }
}

@media (max-width: 731px) {
  .header {
    .header_post {
      max-width: 100%;
      img {
        max-width: 144px;
      }
    }
  }
}
