.category_list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0;
  li {
    list-style: none;
    background-color: #ffffff;
    padding: 9px 14px;
    border-radius: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Nunito;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
    transition: 0.3s;
    border: 1px solid #ffffff;

    &:hover {
      cursor: pointer;
      border: 1px solid #f86310;
    }
    img {
      margin-right: 8px;
    }
    &.active {
      background-color: var(--category-bg);
      color: #ffffff;
    }
  }
}

@media (max-width: 1035px) {
  .category_list {
    flex-wrap: wrap;
    justify-content: flex-start;
    li {
      margin-top: 20px;
      margin-right: 20px;
    }
  }
}
