.menu {
  margin-left: 30px;
  margin-top: 50px;
  width: 80%;
  @media (max-width: 713px) {
    & {
      margin-left: 0;
      width: 100%;
    }
  }
  .card {
    width: 260px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 12px;
    border-radius: 18px;
    margin-top: 28px;
    @media (max-width: 713px) {
      & {
        width: 40%;
      }
    }

    .card_top {
      display: flex;
      flex-direction: column;
      justify-content: center;
      p {
        font-family: Nunito;
        font-size: 24px;
        font-weight: 600;
        line-height: 24px;
        text-align: left;
        margin-top: 16px;
      }
      h1 {
        font-family: Nunito;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        margin-top: 8px;
        margin-bottom: 30px;
      }
    }
    .card_bottom {
      display: flex;
      flex-direction: column;
      span {
        font-family: Nunito;
        font-size: 16px;
        font-weight: 600;
        line-height: 21px;
        color: #b1b1b1;
      }
      button {
        background-color: var(--btn-bg-back);
        border-radius: 12px;
        font-family: Nunito;
        font-size: 16px;
        font-weight: 400;
        line-height: 16px;
        margin-top: 8px;
        height: 40px;
        border: none;
        outline: none;
        cursor: pointer;
        transition: 0.3s;
        &:hover {
          background-color: var(--btn-bg);
          color: #fff;
        }
      }
    }
  }
  .menu_cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (max-width: 713px) {
      svg {
        width: 40%;
        height: 100%;
      }
    }
  }
}
